import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux"
import { deleteAllProducts } from "../../_actions";


const CartSidebar = () => {
    const dispatch = useDispatch();

    const cartProducts = useSelector(state => state.cartProducts);
    const promoArr = useSelector(state => state.promoArr);

    const [totalPrice, setTotalPrice] = useState(0);
    const [totalProducts, setProductsCount] = useState(0)

    const [promoValue, setPromoValue] = useState('')
    const [promo, setPromo] = useState(1);
    
    const [disable, setDisable] = useState(false)

    // state manager

    useEffect(() => {
        const priceProducts = cartProducts.reduce((total, item) => {
            const {price, count} = item;
           return total + price * count
        }, 0);

        setTotalPrice(totalPrice => Math.ceil(priceProducts * promo * 100) / 100);
    }, [cartProducts, promo])

    useEffect(() => {
        const productsCount = cartProducts.reduce((total, item) => {
            const {count} = item;
            return total + count;
        }, 0);

        setProductsCount(totalProducts => productsCount)
    }, [cartProducts])

    useEffect(() => {
        if (cartProducts.length === 0) {
            setDisable(true)
        }
        if (disable === false && cartProducts.length > 0)  {
            setDisable(false)
        }
    }, [cartProducts])

    const onPromo = (event) => {
        const value = event.target.value;
        setPromoValue(promoValue => value);

        promoArr.forEach(promoCode => {
            if (value === promoCode) {
                setPromo(promo => 0.9)
            } else {
                setPromo(promo => 1)
            }
        })
    }

    const createOrder = () => {
        if (cartProducts.length === 0) return;
        dispatch(deleteAllProducts());
        setPromoValue(promoValue => '')
    }

    return (
            <div className="cart__sidebar">
                <h4 className="cart__sidebar-title">Total:</h4>
                <span className="cart__sidebar-total">Price: {totalPrice} $</span>
                <span className="cart__sidebar-total">Total goods: {totalProducts}</span>
                <input className="cart__sidebar-input" 
                       placeholder="Promo code"
                       value={promoValue}
                       onChange={(event) => onPromo(event)}/>
                <button onClick={() => createOrder()}
                        disabled={disable}
                        className={!disable ? 'cart__sidebar-button' : 
                                              'cart__sidebar-button cart__sidebar-button--disable'}>Checkout</button>
            </div>
    )
}

export default CartSidebar;