import './productList.scss'
import {useHttpHook} from '../../myHooks/useHttpHook'
import ProductListItem from '../productListItem/ProductListItem'
import { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { v4 as uuidv4 } from 'uuid';
import { fetchedProducts, fetchingProductsError } from '../../_actions';
const ProductList = () => {
    const dispatch = useDispatch();
    const products = useSelector((state) => state.products);
    const [disable, setDisable] = useState(false);
    const {request} = useHttpHook();
    
    useEffect(() => {

        loadItems();

    }, [])

    const loadItems = () => {
        const itemsCount = products.length === 0 ? 8 : 4;
        // API dosn't have method fot getting range products
            
        request(`https://fakestoreapi.com/products?limit=${ products.length + itemsCount }`)
        .then(res => {
            if (res.length === 20) {
                setDisable(disable => true);
            }

            res = transformResponse(res);

            dispatch(fetchedProducts(res))
        })
        .catch(err => dispatch(fetchingProductsError()))
    }

    const transformResponse = (res) => {
        return res.slice(products.length, res.length);
    }

    const renderItems = (arr) => {
        // if (arr === 'undefined') return null;
        return arr.map(item => {
            return (
                <ProductListItem key={uuidv4()}
                                 id={item.id}
                                 title={item.title}
                                 image={item.image}
                                 descr={item.description}
                                 price={item.price}
                                 rate={item.rating.rate}></ProductListItem>
            )
        })
    }
    const items = renderItems(products)
    return (
        <>
            <ul className='product-list'>
                {items}
            </ul>
            <button disabled={disable}
                    className={!disable ? 'product-list__button' : 
                                          'product-list__button product-list__button--disable'}
                    onClick={(e) => loadItems(e)}>Загрузить больше товаров</button>
        </>
    )
}

export default ProductList;