



export const fetchingProducts = () => {
    return {
        type: 'FETCHING__PRODUCTS'
    }
}

export const fetchedProducts = (products) => {
    return {
        type: 'FETCHED__PRODUCTS',
        payload: products
    }
}

export const fetchingProductsError = () => {
    return {
        type: 'FETCHING__PRODUCTS__ERROR'
    }
}








// cartList

export const deleteProduct = (id) => {
    return {
        type: 'DELETE_PRODUCT_FROM_CART',
        payload: id
    }
}

export const changeCountProduct = (obj) => {
    return {
        type: 'CHANGE_PRODUCT_COUNT',
        payload: obj
    }
}


export const deleteAllProducts = () => {
    return {
        type: 'DELETE_ALL_PRODUCTS_FROM_CART'
    }
}