const initialState = {
    products: [],
    cartProducts: [{title: 'tiw', price: 1222, count: 25, id: '12sdwdwwdfeds'}, {title: 'titltlwsldaff', price: 122, count: 5, id: '12sdwdwdwfeds'}, {title: 'titltlwslfefedaff', price: 122, count: 5, id: '12sdwdwwdfeddws'}, {title: 'titltlwslfefedaff', price: 122, count: 5, id: '12sdwdwwdfedqqqs'}, {title: 'titltlwslfefedaff', price: 122, count: 5, id: '12sdwdqqqqwwdfeds'}, {title: 'titltlwslfefedaff', price: 122, count: 5, id: '12sdwdwwdfed212s'}, {title: 'titltlwslfefedaff', price: 122, count: 5, id: '12sdw21424141dwwdfeds'}, {title: 'titltlwslfefedaff', price: 122, count: 5, id: '12sdw21424141dwwdfed4111s'}, {title: 'titltlwslfefedaff', price: 122, count: 5, id: '12sdw21424141dwwdf42114eds'}],
    favouriteProducts: [],
    loadingStatus: 'idle',
    promoArr: ['123_ddd', 'hello_worls', 'oleg']
}

const reducer = (state = initialState, action) => {
    switch (action.type) {
        case 'FETCHING__PRODUCTS':
            return {
                ...state,
                loadingStatus: 'loading'
            }
        case 'FETCHED__PRODUCTS':
            return {
                ...state,
                products: [...state.products ,...action.payload],
                loadingStatus: 'idle'
            }
        case 'FETCHING__PRODUCTS__ERROR':
            return {
                ...state,
                loadingStatus: 'error'
            }
        case 'DELETE_PRODUCT_FROM_CART':
            const {cartProducts} = state;
            const newCartList = cartProducts.filter(item => {
                return item.id !== action.payload
            })
            return {
                ...state,
                cartProducts: newCartList 
            }
        case 'CHANGE_PRODUCT_COUNT':
            const index = state.cartProducts.findIndex(product => {
                return product.id === action.payload.id
            })
            const arr = state.cartProducts.map((item, k) => {
                return k !== index ? item : {
                    ...item,
                    count: action.payload.count
                }
            })
            return {
                ...state,
                cartProducts: arr
            }
        case 'DELETE_ALL_PRODUCTS_FROM_CART':
            return {
                ...state,
                cartProducts: []
            }
        default:
            return state;
    }
}

export default reducer;