import { useEffect } from 'react';
import { useState } from 'react';
import { useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import './productListItem.scss';
import Rating from './rating/Rating'

const ProductListItem = ({id, title, image, descr, price, rate}) => {
    
    const shortTitle = (title) => {
        if (title.length > 20) {
            const newTitle = title.slice(0, 20) + '...'
            if (newTitle[19] === ' ') {
                return newTitle.slice(0,19) + '...'
            }
            return newTitle;
        }
        return title
    }

    return (
        <li>
            <Link to={`/product/${id}`} className='product'>
                <div className='product__img-block'>
                    <img className='product__img' src={image} alt={title}/>
                    <div className='product__rate'>
                        <Rating value={rate}></Rating>
                    </div>
                </div>
                <span className='product__title'>{shortTitle(title)}</span>
                <span className='product__descr'></span>
                <span className='product__price'>{price} $</span>
            </Link>
        </li>
    )
}

export default ProductListItem;

